.home{
  background-color:#ffbebe !important;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  /* height: 100%; */
  /* -webkit-background-size: cover ; */
  /* background-attachment: fixed; */
}
.content{
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  color: white;
}
.btn-group{
  position: absolute;
  bottom: 20px;
}
.tab-btn{
  /* border-bottom: 2px solid gray; */
  border-radius: 5px;
  border: 1px solid gray;
  padding: 0 5px 0 5px;
}
.btn_checked{
  background: rgb(230, 208, 208);
}
.list-item{
  border: 1px rgb(112, 111, 111) solid;
  border-radius: 5px;
}
.check-item{
  border: 1px solid gray;
  width: 25px;
  height: 25px;
  border-radius: 50%;
}
.progress-text{
  -webkit-transform: translateY(6px);
          transform: translateY(6px);
}
.item-checked{
  border-bottom: 5px solid rgb(255, 255, 255);
  border-right: 5px solid rgb(255, 255, 255);
  height: 25px;
  width: 13px;
  -webkit-transform: rotate(45deg) translate(0px, -10px);
          transform: rotate(45deg) translate(0px, -10px);
}
.model{
  background-color: rgba(0,0,0,0.5) !important;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.loading {
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
  /* background: url('../assets/img/bg_dark.png') no-repeat; */
  width: 100vw;
  height: 100vh;
  z-index: 1000;
}
.circle{
  width: 3px;
  height: 10px;
  border-radius: 35%;
  background: white;
  -webkit-animation: dot 0.6s infinite;
          animation: dot 0.6s infinite;
}
.circle:nth-child(1){
  -webkit-animation-delay:0.1s;
          animation-delay:0.1s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(0deg) translateY(2px);
          transform: rotate(0deg) translateY(2px);
}
.circle:nth-child(2){
  -webkit-animation-delay:0.2s;
          animation-delay:0.2s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(60deg) translate(-1.5px, 4.5px);
          transform: rotate(60deg) translate(-1.5px, 4.5px);
}
.circle:nth-child(3){
  -webkit-animation-delay:0.3s;
          animation-delay:0.3s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(120deg) translate(-1.5px, 6px);
          transform: rotate(120deg) translate(-1.5px, 6px);
}
.circle:nth-child(4){
  -webkit-animation-delay:0.4s;
          animation-delay:0.4s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(180deg) translateY(6px);
          transform: rotate(180deg) translateY(6px);
}
.circle:nth-child(5){
  -webkit-animation-delay:0.5s;
          animation-delay:0.5s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(240deg) translate(1.5px, 6px);
          transform: rotate(240deg) translate(1.5px, 6px);
}
.circle:nth-child(6){
  -webkit-animation-delay:0.6s;
          animation-delay:0.6s;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotate(300deg) translate(1.5px, 4.5px);
          transform: rotate(300deg) translate(1.5px, 4.5px);
}

